import React from "react";
import { View, Text, Image } from "react-native";
import {
  TouchableWithoutFeedback,
  TouchableOpacity,
} from "react-native-gesture-handler";
const Cards = (props) => {
  var icon;
  if (props.imageurl === "1") {
    icon = require("../assets/1.png");
  } else if (props.imageurl === "3") {
    icon = require("../assets/3.png");
  } else if (props.imageurl === "5") {
    icon = require("../assets/5.png");
  } else if (props.imageurl === "9") {
    icon = require("../assets/9.png");
  } else if (props.imageurl === "4") {
    icon = require("../assets/4.png");
  } else if (props.imageurl === "8") {
    icon = require("../assets/8.png");
  } else if (props.imageurl === "7") {
    icon = require("../assets/7.png");
  }

  return (
    <View
      style={{
        flexDirection: "column",
        alignItems: "stretch",
        flex: 1,
        margin: 10,
        marginTop: 8,
        borderRadius: 30,
        shadowColor: "#000",
        shadowOffset: {
          width: 0,
          height: 2,
        },
        shadowOpacity: 0.25,
        shadowRadius: 3.84,
        elevation: 5,
        overflow: "hidden",
      }}
    >
      <TouchableOpacity
        onPress={() =>
          props.navigation.navigate("Excercise", {
            cardtitle: props.exname,
            cardurl: props.cardurl,
            info: props.info,
          })
        }
      >
        <View style={{ borderRadius: 40, overflow: "hidden" }}>
          <Image
            resizeMode="contain"
            // borderRadius={100}
            source={icon}
            style={{
              flex: 1,
              width: "100%",
              height: 200,
              borderRadius: 30,
              backgroundColor: props.backgroundcolor,
              overflow: "hidden",
            }}
          />
        </View>
      </TouchableOpacity>
    </View>
  );
};

export default Cards;
