import React from "react";
import { View, Text, Image, ScrollView } from "react-native";
import { TouchableWithoutFeedback } from "react-native";
import Cards from "../Cards";
import Header from "../components/Header";
const HomeCard = (props) => {
  return (
    <ScrollView
      style={{
        flex: 1,
        flexDirection: "column",
      }}
    >
      <View
        style={{
          flex: 1,
        }}
      >
        <Header />

        <Cards
          imageurl={"1"}
          backgroundcolor={"#81d4fa"}
          cardurl={"1"}
          {...props}
          exname="pushups"
          info="Pushups are a fast and effective exercise for building strength,Traditional pushups are beneficial for building upper body strength. "
        />

        <View>
          <View
            style={{
              flexDirection: "row",
              alignItems: "center",
              justifyContent: "space-around",
            }}
          >
            <Cards
              imageurl={"3"}
              backgroundcolor={"#b2ff59"}
              {...props}
              exname="crunches"
              cardurl={"3"}
              info=" crunches help you build muscle. But unlike situps, they work only the abdominal muscles.which includes your lower back muscles and obliques."
            />
            <Cards
              imageurl={"5"}
              backgroundcolor={"#fdd835"}
              {...props}
              cardurl={"5"}
              exname="V-sit"
              info="The V-sit ab exercise builds core strength by working multiple areas of the core at the same time, while also challenging your balance. In this exercise, you sit with legs extended and torso off the ground, your body forming a V shape."
            />
          </View>
          <View
            style={{
              flexDirection: "row",
              alignItems: "center",
              justifyContent: "space-around",
            }}
          >
            <Cards
              imageurl={"9"}
              backgroundcolor={"#f06292"}
              {...props}
              cardurl={"9"}
              exname="tree Pose"
              info="Tree Pose stretches the thighs, groins, torso, and shoulders. It builds strength in the ankles and calves, and tones the abdominal muscles. "
            />

            <Cards
              imageurl={"4"}
              backgroundcolor={"#0288d1"}
              {...props}
              cardurl={"4"}
              exname="tree Pose"
              info="Tree Pose stretches the thighs, groins, torso, and shoulders. It builds strength in the ankles and calves, and tones the abdominal muscles. "
            />
          </View>
          <View
            style={{
              flexDirection: "row",
              alignItems: "center",
              justifyContent: "space-around",
            }}
          >
            <Cards
              imageurl={"8"}
              backgroundcolor={"#b2ff59"}
              {...props}
              exname="Plank"
              cardurl={"8"}
              info="The plank is an isometric core strength exercise that involves maintaining a position similar to a push-up for the maximum possible time"
            />

            <Cards
              imageurl={"7"}
              backgroundcolor={"#fdd835"}
              {...props}
              exname="dumbbell shoulder press"
              cardurl={"7"}
              info="Traditional pushups are beneficial for building upper body strength. They work the triceps, pectoral muscles, and shoulders."
            />
          </View>
        </View>
      </View>
    </ScrollView>
  );
};

export default HomeCard;
