import React from "react";
import { View, Text, Image, Dimensions } from "react-native";
const HomeCard = () => {
  return (
    <View
      style={{
        width: "100%",
        height: 80,
        backgroundColor: "white",
        shadowOpacity: 0.44,
        shadowRadius: 8,
        elevation: 10,
        flexDirection: "row",
        shadowColor: "#000",
      }}
    >
      <View
        style={{
          flex: 1,
          justifyContent: "center",
          alignContent: "center",
          alignSelf: "center",
          flexDirection: "row",
          marginTop: 15,
          marginBottom: 0,
        }}
      >
        <Image
          source={require("../../assets/LCOT.png")}
          style={{
            width: 100,
            height: 60,
            justifyContent: "center",
          }}
        />
      </View>
      {/* <Text
        style={{
          alignItems: "center",
          marginTop: 13,
          marginLeft: 4,
          fontSize: 15,
        }}
      >
        WORKOUT
      </Text> */}
    </View>
  );
};

export default HomeCard;
