import * as React from "react";
import { View, Text, Image, ScrollView, Button, Linking } from "react-native";
import Cards from "../Cards";
import { Audio } from "expo-av";
import AntIcon from "react-native-vector-icons/AntDesign";

class Excercise extends React.Component {
  constructor(props) {
    super(props);
  }

  async loadmusic() {
    try {
      await this.soundObject.loadAsync(
        require("../../assets/audio/Song 14 - Rock.mp3")
      );
      await this.soundObject2.loadAsync(
        require("../../assets/audio/Song 18 - Rock.mp3")
      );
      await this.soundObject3.loadAsync(
        require("../../assets/audio/Song 34 - Pop.mp3")
      );
    } catch (error) {
      console.log("problem ", error);
    }
  }
  UNSAFE_componentWillMount() {
    this.loadmusic();
    console.log(this.props);
  }

  soundObject = new Audio.Sound();
  soundObject2 = new Audio.Sound();
  soundObject3 = new Audio.Sound();
  componentWillUnmount() {
    this.soundObject.stopAsync();
    this.soundObject2.stopAsync();
    this.soundObject3.stopAsync();
    this.soundObject._clearSubscriptions();
    this.soundObject2._clearSubscriptions();
    this.soundObject3._clearSubscriptions();
    clearInterval(this.state.timer);
  }
  // playmusic = async () => {};
  pausemusic = async () => {
    this.soundObject.pauseAsync();
  };
  state = {
    timer: null,
    minutes_Counter: "00",
    seconds_Counter: "00",
    startDisable: false,
    laps: 1,
    gymstate: "LAP 1",
    songaname: "Song 14 - Rock.mp3",
  };
  onButtonStart = () => {
    if (this.state.laps < 4) {
      if (this.state.laps === 1) {
        this.soundObject.playAsync();
      } else if (this.state.laps === 2) {
        this.soundObject2.playAsync();
      } else if (this.state.laps === 3) {
        this.soundObject3.playAsync();
      }

      console.log("called");
      this.setState({ gymstate: "LAP " + this.state.laps });
      let timer = setInterval(() => {
        var num = (Number(this.state.seconds_Counter) + 1).toString(),
          count = this.state.minutes_Counter;
        console.log(this.state.minutes_Counter);
        if (Number(this.state.seconds_Counter) == 59) {
          count = (Number(this.state.minutes_Counter) + 1).toString();
          num = "00";
        }
        if (this.state.minutes_Counter === "01") {
          count = "00";
          num = "00";
          this.onButtonStop();
          this.setState({
            timer: null,
            minutes_Counter: "00",
            seconds_Counter: "00",
          });
        }

        this.setState({
          minutes_Counter: count.length == 1 ? "0" + count : count,
          seconds_Counter: num.length == 1 ? "0" + num : num,
        });
      }, 1000);
      this.setState({ timer });

      this.setState({ startDisable: true });
    } else {
      this.state.gymstate = "Completed";
      this.pausemusic();
    }
  };
  onButtonStop = () => {
    clearInterval(this.state.timer);
    this.setState({ startDisable: false });
    this.setState({ laps: this.state.laps + 1 });
    this.soundObject.stopAsync();
    this.soundObject2.stopAsync();
    this.soundObject3.stopAsync();
    this.setState({ gymstate: "BREAK" });
    if (this.state.laps <= 3) {
      setTimeout(this.onButtonStart, 10000);
    } else {
      this.state.gymstate = "Completed";
    }
    console.log(this.state.laps);
  };

  onButtonClear = () => {
    this.setState({
      timer: null,
      minutes_Counter: "00",
      seconds_Counter: "00",
    });
  };

  render() {
    return (
      //props.route.params.cardurl
      <ScrollView>
        <View>
          <Cards
            imageurl={this.props.route.params.cardurl}
            backgroundcolor={"#81d4fa"}
            {...this.props}
            exname="dips"
          />
          <View
            style={{ flexDirection: "row", justifyContent: "space-between" }}
          >
            <Text style={{ marginLeft: 20, fontSize: 20, fontWeight: "500" }}>
              {this.props.route.params.cardtitle}
            </Text>
            <View style={{ marginRight: 10 }}>
              <Text style={{ fontSize: 12 }}>03 Min</Text>
              <Text style={{ fontSize: 10 }}>Duration</Text>
            </View>
          </View>
          <View style={{ margin: 15 }}>
            <Text>{this.props.route.params.info}</Text>
          </View>
          <Button
            title="START"
            color="#64dd17"
            onPress={() => {
              this.onButtonStart();
            }}
          />
          <View
            style={{
              flexDirection: "row",
              justifyContent: "center",
            }}
          >
            <Text style={{ fontSize: 50, fontWeight: "900", color: "#ec407a" }}>
              {this.state.minutes_Counter}{" "}
            </Text>
            <Text style={{ fontSize: 50, fontWeight: "900", color: "#ff7043" }}>
              : {this.state.seconds_Counter}
            </Text>
          </View>
          <Text
            style={{
              fontSize: 50,
              alignSelf: "center",
              color: "#afb42b",
              fontWeight: "800",
            }}
          >
            {this.state.gymstate}
          </Text>
        </View>
      </ScrollView>
    );
  }
}
export default Excercise;
